import React, { useEffect, useMemo, useState } from 'react'
import {
	Box,
	TabPanels,
	TabList,
	Tab,
	Tabs,
	TabPanel,
	Input,
	InputGroup,
	InputRightElement,
	Button,
	Stack,
	useBoolean,
	Accordion,
	AccordionItem,
	AccordionButton,
	AccordionIcon,
	AccordionPanel,
	Badge,
	Text,
	Center,
	Divider,
	Spinner,
	Progress,
	Link,
} from '@chakra-ui/react'
import { useApiContract } from 'react-moralis'
import {
	AddIcon,
	ArrowForwardIcon,
	ArrowRightIcon,
	CheckIcon,
	SearchIcon,
} from '@chakra-ui/icons'
import abi from './abi.json'

const TLDs = ['ava', 'web3', 'dapp', 'ipfs', 'defi', 'gamefi']

function DateFormat(props: { timestamp: string }) {
	const { timestamp } = props
	const d = new Date(parseInt(timestamp, 10) * 1000)
	return (
		<Text>{`${d.getDate() < 10 ? `0${d.getDate()}` : d.getDate()}/${
			d.getMonth() + 1 < 10 ? `0${d.getMonth() + 1}` : d.getMonth() + 1
		}/${d.getFullYear()}`}</Text>
	)
}

function DomainItem(props: {
	adderessList:
		| [[string, string, string, string, string, string, string, string]]
		| string
		| null
}) {
	const { adderessList } = props
	return (
		<Accordion allowToggle mt={5}>
			{(adderessList && typeof adderessList === 'object' ? adderessList : []).map(
				fullDomain => {
					return fullDomain[1] === '' ? (
						''
					) : (
						<AccordionItem border={0} my={3} key={fullDomain[1]}>
							<h2>
								<AccordionButton
									bgGradient='linear(to-r, red.700, pink.800)'
									borderRadius='md'
									_hover={{ bgGradient: 'linear(to-r, red.600, pink.700)' }}
								>
									<Box flex='1' textAlign='left' isTruncated>
										<ArrowForwardIcon mr={5} fontSize='2xl' />
										{`${fullDomain[1]}.${TLDs[parseInt(fullDomain[0], 10)]}`}
									</Box>
									<AccordionIcon />
								</AccordionButton>
							</h2>
							<AccordionPanel pb={4}>
								<Center
									py={5}
									bgGradient='linear(to-r, pink.800, pink.700)'
									borderRadius='md'
									mb={5}
								>
									<Text fontWeight='extrabold'>
										<Link
											color='white'
											href={`http://${fullDomain[1]}.${TLDs[parseInt(fullDomain[0], 10)]}`}
											target='_blank'
										>
											{fullDomain[1]}.{TLDs[parseInt(fullDomain[0], 10)]}
										</Link>
									</Text>
									<ArrowRightIcon mx={5} />
									<Text fontWeight='extrabold'>
										<Link
											color='white'
											href={`https://dweb.link/ipfs/${fullDomain[2]}`}
											target='_blank'
										>
											ipfs://{fullDomain[2].slice(0, 3)}...{fullDomain[2].slice(-3)}
										</Link>
									</Text>
								</Center>
								<Stack spacing={6} direction={['row']}>
									<Box width='50%' textAlign='center' title={fullDomain[4]}>
										Transfer{' '}
										{fullDomain[4] === '0x0000000000000000000000000000000000000000' ? (
											<Badge variant='subtle' colorScheme='red'>
												LOCKED
											</Badge>
										) : (
											<>
												for{' '}
												<Badge variant='subtle' colorScheme='yellow'>
													{fullDomain[4].slice(0, 3)}...{fullDomain[4].slice(-3)}
												</Badge>
											</>
										)}
									</Box>
									<Box width='50%' textAlign='center'>
										Price{' '}
										{fullDomain[5] === '0' ? (
											<Badge variant='subtle' colorScheme='red'>
												NOT SALE
											</Badge>
										) : (
											<>
												<Badge variant='subtle' colorScheme='teal'>
													{(parseInt(fullDomain[5], 10) / 10 ** 18).toFixed(3)}
												</Badge>{' '}
												<Badge variant='subtle' colorScheme='pink'>
													AVAX
												</Badge>
											</>
										)}
									</Box>
								</Stack>
								<Divider my={5} colorScheme='purple' variant='dashed' />
								<Stack spacing={6} direction={['row']}>
									<DateFormat timestamp={fullDomain[6]} />
									<Progress
										colorScheme='red'
										height={4}
										width='100%'
										value={
											10 +
											((Math.floor(Date.now() / 1000) - parseInt(fullDomain[6], 10)) *
												100) /
												(parseInt(fullDomain[7], 10) - parseInt(fullDomain[6], 10))
										}
										borderRadius='lg'
									/>
									<DateFormat timestamp={fullDomain[7]} />
								</Stack>
							</AccordionPanel>
						</AccordionItem>
					)
				}
			)}
		</Accordion>
	)
}

function App(): React.ReactElement {
	const [valid, setValid] = useBoolean()
	const [ipfsGatawey, setIpfsGatawey] = useState('dweb.link')
	const [params, setParams] = useState({
		owner: '',
		page: 1,
		limit: 10,
	})

	const handleChangeAddress = (event: React.ChangeEvent<HTMLInputElement>) => {
		setParams({ owner: event.target.value, page: 1, limit: 10 })
		if (event.target.value.length === 42) {
			setValid.on()
		}
	}

	const handleChangeIpfsGatawey = (event: React.ChangeEvent<HTMLInputElement>) =>
		setIpfsGatawey(event.target.value)

	const {
		runContractFunction: getUserDomains,
		data: adderessList,
		isFetching,
		isLoading,
	} = useApiContract({
		address: '0x8B860047795E9A42c2E9D568867c03a22132CB4E',
		functionName: 'getDomains',
		chain: 'avalanche testnet',
		abi,
		params,
	})

	useEffect(() => {
		if (typeof chrome !== 'undefined' && typeof chrome.storage !== 'undefined') {
			chrome.storage.sync.get(['getParams'], result => {
				if (result && result.getParams) {
					setParams(result.getParams)
				}
			})
			chrome.storage.sync.get(['ipfsGatawey'], result => {
				if (result && result.ipfsGatawey) {
					setIpfsGatawey(result.ipfsGatawey)
				}
			})
		}
	}, [])

	useEffect(() => {
		if (typeof chrome !== 'undefined' && typeof chrome.storage !== 'undefined') {
			if (
				typeof params !== 'undefined' &&
				typeof params.owner !== 'undefined' &&
				params.owner.length === 42
			) {
				if (params.owner === '0x0000000000000000000000000000000000000000') {
					setValid.off()
					params.owner = ''
				}
				chrome.storage.sync.set({ getParams: params }, () => {
					if (params.owner) getUserDomains()
					console.log('Saved:', params)
				})
			}
			if (typeof ipfsGatawey !== 'undefined' && ipfsGatawey) {
				chrome.storage.sync.set({ ipfsGatawey }, () => {
					console.log('Saved:', ipfsGatawey)
				})
			}
		}
	}, [params, ipfsGatawey]) // eslint-disable-line react-hooks/exhaustive-deps

	const PlaceholderTextLoading = () =>
		useMemo(
			() =>
				isFetching || isLoading ? (
					<Box my={10} textAlign='center'>
						<Spinner size='xl' color='pink.300' />
					</Box>
				) : (
					<Box my={10} textAlign='center'>
						<Text
							bgGradient='linear(to-l, #40E0D0, #FF8C00, #FF0080)'
							bgClip='text'
							fontWeight='extrabold'
						>
							Enter{' '}
							<Badge variant='outline' colorScheme='red' mx={1}>
								AVAX
							</Badge>{' '}
							address and you <br />
							will see all registered domains.
						</Text>
					</Box>
				),
			[]
		)

	return (
		<Box
			w='100vw'
			h='100vh'
			minW='480px'
			minH='600px'
			color='white'
			bgGradient='linear(to-r, red.900, pink.800)'
			m={0}
			p={6}
			borderRadius='none'
			overflow='auto'
		>
			<Center height='30px' mb={5} borderRadius='lg'>
				<Box mx={5}>
					<Text
						bgGradient='radial(pink.400, red.800)'
						bgClip='text'
						fontSize='xl'
						fontWeight='extrabold'
					>
						AVADoma
					</Text>
				</Box>
				<Divider orientation='vertical' />
				<Box mx={5} fontSize={10} color='red.300'>
					BETA / Testnet
				</Box>
			</Center>
			<Tabs isFitted variant='unstyled' size='md'>
				<TabList>
					<Tab
						boxShadow='base'
						color='white'
						_hover={{
							color: 'white',
							bgGradient: 'linear(to-r, pink.800, red.700)',
							borderRadius: 'md',
						}}
						_selected={{
							color: 'white',
							bgGradient: 'linear(to-r, red.700, pink.800)',
							borderRadius: 'md',
						}}
						margin={1}
						fontWeight='extrabold'
					>
						Domains
					</Tab>
					<Tab
						boxShadow='base'
						color='white'
						_hover={{
							color: 'white',
							bgGradient: 'linear(to-r, pink.800, red.700)',
							borderRadius: 'md',
						}}
						_selected={{
							color: 'white',
							bgGradient: 'linear(to-r, red.700, pink.800)',
							borderRadius: 'md',
						}}
						margin={1}
						fontWeight='extrabold'
					>
						Wallet
					</Tab>
					<Tab
						boxShadow='base'
						color='white'
						_hover={{
							color: 'white',
							bgGradient: 'linear(to-r, pink.800, red.700)',
							borderRadius: 'md',
						}}
						_selected={{
							color: 'white',
							bgGradient: 'linear(to-r, red.700, pink.800)',
							borderRadius: 'md',
						}}
						margin={1}
						fontWeight='extrabold'
					>
						IPFS
					</Tab>
				</TabList>
				<TabPanels>
					<TabPanel>
						<Stack spacing={6} direction={['column', 'row']}>
							<InputGroup size='lg'>
								<Input
									type='text'
									value={params.owner}
									onChange={handleChangeAddress}
									placeholder='AVAX Address'
									color='red.200'
									focusBorderColor='red.600'
									borderColor='red.700'
									_hover={{ borderColor: 'red.600' }}
									_placeholder={{ color: 'red.600' }}
									spellCheck='false'
								/>
								{valid && (
									<InputRightElement>
										<CheckIcon color='green.300' />
									</InputRightElement>
								)}
							</InputGroup>
							<Button
								variant='outline'
								colorScheme='red'
								size='lg'
								onClick={() => getUserDomains()}
								color='red.200'
								border={0}
								bgGradient='linear(to-r, red.700, pink.800)'
								_hover={{
									bgGradient: 'linear(to-r, red.600, pink.700)',
									color: 'white',
								}}
								_active={{
									bgGradien–µ: 'linear(to-r, red.700, pink.800)',
								}}
							>
								<SearchIcon />
							</Button>
						</Stack>
						{adderessList ? (
							<DomainItem adderessList={adderessList} />
						) : (
							<PlaceholderTextLoading />
						)}
						<Box py={1} textAlign='center'>
							<Link href='https://avadoma.com' target='_blank'>
								<Button
									rightIcon={<AddIcon />}
									colorScheme='red'
									variant='outline'
									mt={5}
									color='red.200'
									bgGradient='linear(to-r, red.700, pink.800)'
									_hover={{
										bgGradient: 'linear(to-r, red.600, pink.700)',
										color: 'white',
									}}
									_active={{
										bgGradien–µ: 'linear(to-r, red.700, pink.800)',
									}}
								>
									Register new domain
								</Button>
							</Link>
						</Box>
					</TabPanel>
					<TabPanel>
						<Box py={10} textAlign='center'>
							<Text
								bgGradient='linear(to-l, #40E0D0, #FF8C00, #FF0080)'
								bgClip='text'
								fontWeight='extrabold'
							>
								The{' '}
								<Badge variant='outline' colorScheme='red' mx={1}>
									AVAX
								</Badge>{' '}
								wallet and{' '}
								<Badge variant='outline' colorScheme='pink' mx={1}>
									IPFS
								</Badge>{' '}
								integration
								<br /> with{' '}
								<Badge variant='outline' colorScheme='yellow' mx={1}>
									dApps
								</Badge>{' '}
								will appear soon.
							</Text>
						</Box>
					</TabPanel>
					<TabPanel>
						<Input
							type='text'
							size='lg'
							value={ipfsGatawey}
							onChange={handleChangeIpfsGatawey}
							placeholder='IPFS Gateway'
							color='red.200'
							focusBorderColor='red.600'
							borderColor='red.700'
							_hover={{ borderColor: 'red.600' }}
							_placeholder={{ color: 'red.600' }}
							spellCheck='false'
						/>
						<Box py={10} textAlign='center'>
							<Text
								bgGradient='linear(to-l, #40E0D0, #FF8C00, #FF0080)'
								bgClip='text'
								fontWeight='extrabold'
							>
								Through this{' '}
								<Badge variant='outline' colorScheme='pink' mx={1}>
									IPFS
								</Badge>{' '}
								gateway, you will access{' '}
								<Badge variant='outline' colorScheme='yellow' mx={1}>
									dApps
								</Badge>
							</Text>
						</Box>
					</TabPanel>
				</TabPanels>
			</Tabs>
		</Box>
	)
}

export default App
